@use '_partials' as *;
*, *::before, *:after{
    box-sizing: border-box;
}
body, h1, h2, h3, h4, h5, h6, p{margin: 0;}

html{font-size: 10px;}

body{
    display: grid;
    place-items: center;
    min-height: 100vh;
    font-size: 1.5rem;
    font-family: 'Spartan', sans-serif;
}

.container{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    width: 70%;
   

    .top{
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        color: $very-dark;
        min-height: 45%;
        // height: 50%;
        margin-bottom: 5rem;

        .top-left{
            display: flex;
            flex-flow: column;
            justify-content: space-between;
            width: 40%;

            h1{
                font-weight: bolder;
                font-size: 5.5rem;
                line-height: 1;
    

            }

            p{ 
                margin-top: 1rem;
                font-weight: 500;
                line-height: 1.5;
            }
        }
        .top-right{
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: space-between;
            width: 45%;
           

            .flex{
                display: flex;
                justify-content: space-around;
                align-items: center;
                padding: 1rem;
                background: $light-grayish;
                border-radius: .7rem;
                width: 90%;
                height: 26%;

                .all-star{
                    display: flex;
                    justify-content: space-between;
                    width: 30%;

                    .star{
                      width: 15%;
                     
                    }
                }

                h5{
                    width: 65%;
                }
            }

            .start{
                align-self: flex-start;
            }
            .middle{
                align-self: center;
            }
            .end{
                align-self: flex-end;
            }

        }
    }
    .bottom{
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        min-height: 30rem;
        // height: 45%;

        .card{
            display: flex;
            flex-flow: column;
            justify-content: space-around;
            width: 31.333%;
            min-height: 25rem;
            background: $very-dark;
            // margin: 1rem;
            border-radius: 1rem;
            padding: 1.6rem; 
            


            .profile{
                display: flex;
                align-items: center;


                .profile-img{
                    margin-right: 1.6rem;
                    text-align: center;
                  
                    
                    img{
                       display: inline-block;
                        border-radius: 50%;
                        width: 70%;
                    }
                }

                .profile-name{
                               
                    h5{
                        color: $pink;
                        font-weight: 400;
                    }
                    h4{
                        color: $white;
                        font-weight: 500;
                    }
                }

                
            }

            .text{
                color: white;
                line-height: 1.5;
            }
        }

        .start{
            align-self: flex-start;
        }
        .middle{
            align-self: center;
        }
        .end{
            align-self: flex-end;
        }
        
    }
}

@media screen and (min-width: 1281px) and (max-width: 1440px) {
    .container{
        width: 80%;
    }
}

@media screen and (min-width: 1151px) and (max-width: 1280px){
    .container {
        width: 90%;
    }
}

@media screen and (max-width: 1150px) {
    .container{
        width: 100%;
        margin: 4rem;
        align-items: center;


        .top{
            flex-direction: column;
            align-items: center;
            width: 90%;

            .top-left, .top-right{
                width: 100%;
            }
                  

            .top-left{text-align: center;}

          .top-right{
           


              .flex{
                  flex-direction: column;
                  align-items: center;
                  justify-content: space-between;
                  width: 100%;
                  margin: 1.6rem 0;
                //   background: yellow;

                  .all-star, h5{
                      width: 100%;
                  }

                  .all-star{
                      justify-content: center;
                    //   background-color: purple;


                      .star{
                          width: 3rem;
                      }
                  }

                  
                  }   
                   h5{
                      text-align: center;
                    //   background-color: pink;
                  }

            .start, .middle, .end{
                align-self: center;
            }
          }
        }

        .bottom{
            flex-direction: column;
            align-items: center;

            .card{
                width: 90%;
                margin-bottom: 1.6rem;
            }
            .start{
                align-self: center;
            }
            .middle{
                align-self: center;
            }
            .end{
                align-self: center;
            }
        }
    }
}